<template>
    <div v-show="showShade" class="again-verify-shade">
        <div class="again-verify">
            <span></span>
            <div class="verify-input">
                <i-input v-model="againVerifyCode" placeholder="" type="password" class="column-input"></i-input>
                <Button style="margin-left: 8px;" type="primary" @click="handleUnlock"></Button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        showShade: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            againVerifyCode: '',
            isLoading: false
        }
    },
    methods: {
        handleUnlock() {
            let that = this;

            if (!that.againVerifyCode) {
                that.$Message.error('');
                return;
            }
            
            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: '/api/Merchant/VerificationSecurityPwd',
                    params: {
                        pwd: that.againVerifyCode
                    }
                }).then(function(data) {
                    that.isLoading = false;
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                        that.againVerifyCode = '';
                        localStorage.setItem('againVerify', false);
                        that.$emit('unlockSuccess');
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                })
            }
        }
    }
}
</script>

<style lang="scss" scoped>
.again-verify-shade {
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    background-color: #000;
    z-index: 999;

    .again-verify {
        color: #ffffff;
        font-size: 14px;
        
        .verify-input {
            display: flex;
            align-items: center;

            margin-top: 20px;
        }
    }
}
</style>