<template>
    <div class="con">
        <div class="header">
            <div class="item logo">
                <img src="../../assets/home/logo.png" alt title/>
            </div>
            <div class="item time">
                <p>{{'' + sysInfo.poptip}}</p>
                <p>{{'' + serveTime}}</p>
            </div>
            <div class="item notice">
                <div behavior="scroll" class="notice-bulletin">
                    <div v-html="sysBulletin"></div>
                </div>
            </div>
            <div class="item user">
                <span></span>
                <span>{{loginName}}</span>
            </div>
            <div class="item money" title="">
                <img src="../../assets/home/huoxb.png" alt="huoxb"/>
                <span>{{marsCurrency}}</span>
            </div>
            <div class="item exit" @click="handleLock">
                <span></span>
            </div>
            <div class="item exit" @click="handleExit">
                <span></span>
            </div>
        </div>
        <div class="nav">
            <div class="nav-list">
                <div :class="['nav-item', index === selectNav ? 'active-item' : '']" v-for="(item, index) in navList" :key="index" @click="handleNav(item, index)">
                    <img :src="index === selectNav ? item.activeImg : item.img" alt title/>
                    <p>{{item.title}}</p>
                </div>
            </div>
        </div>
        <div class="page">
            <router-view></router-view>
        </div>
        <againVerify :showShade="showVerify" @unlockSuccess="handleUnlockSuccess"></againVerify>
    </div>
</template>

<script>
import Bus from '../../tools/eventBus/index.js';
import againVerify from '../../component/againVerify.vue';

export default {
    components: {
        againVerify
    },
    data() {
        return {
            showVerify: false,
            sysInfo: {
                totalProfitAndLoss: 0,
                totalBalanceOfAccount: 0,
                poptip: ''
            },
            navList: [
                {
                    img: require('../../assets/home/index_n.png'),
                    activeImg: require('../../assets/home/index_s.png'),
                    title: '',
                    fontFM: 'jdFontAwesome',
                    href: 'index'
                },
                {
                    img: require('../../assets/home/base_n.png'),
                    activeImg: require('../../assets/home/base_s.png'),
                    title: '',
                    fontFM: 'jdFontAwesome',
                    href: 'base'
                },
                {
                    img: require('../../assets/home/member_n.png'),
                    activeImg: require('../../assets/home/member_s.png'),
                    title: '',
                    fontFM: 'jdFontAwesome',
                    href: 'userManage'
                },
                {
                    img: require('../../assets/home/monitor_n.png'),
                    activeImg: require('../../assets/home/monitor_s.png'),
                    title: '',
                    fontFM: 'jdFontAwesome',
                    href: 'fieldMonitor'
                },
                {
                    img: require('../../assets/home/member_n.png'),
                    activeImg: require('../../assets/home/member_s.png'),
                    title: '',
                    fontFM: 'jdFontAwesome',
                    href: 'reportForm'
                },
                {
                    img: require('../../assets/home/back_n.png'),
                    activeImg: require('../../assets/home/back_s.png'),
                    title: '',
                    fontFM: 'jdFontAwesome',
                    href: 'backwater'
                },
                {
                    img: require('../../assets/home/odds_n.png'),
                    activeImg: require('../../assets/home/odds_s.png'),
                    title: '',
                    fontFM: 'jdFontAwesome',
                    href: 'oddsLimit'
                },
                {
                    img: require('../../assets/home/room_n.png'),
                    activeImg: require('../../assets/home/room_s.png'),
                    title: '',
                    fontFM: 'jdFontCustom',
                    href: 'roomSetting'
                },
                {
                    img: require('../../assets/home/agent_n.png'),
                    activeImg: require('../../assets/home/agent_s.png'),
                    title: '',
                    fontFM: 'jdFontCustom',
                    href: 'agent'
                },
                {
                    img: require('../../assets/home/aReport_n.png'),
                    activeImg: require('../../assets/home/aReport_s.png'),
                    title: '',
                    fontFM: 'jdFontCustom',
                    href: 'agentReport'
                },
                {
                    img: require('../../assets/home/fUser_n.png'),
                    activeImg: require('../../assets/home/fUser_s.png'),
                    title: '',
                    fontFM: 'jdIonicons',
                    href: 'falseUser'
                },
                {
                    img: require('../../assets/home/other_n.png'),
                    activeImg: require('../../assets/home/other_s.png'),
                    title: '',
                    fontFM: 'jdFontCustom',
                    href: 'other'
                }
            ],
            selectNav: 0,
            sysBulletin: '',
            loginName: '',
            serveTime: '',
            marsCurrency: 0,
            balance: '',
            auto: null,
            auto1: null,
            timeout: null,
            timeout1: null
        }
    },
    beforeRouteEnter (to, from, next) {
        // ...
        next(vm => {
            let showVerify_ = localStorage.getItem('againVerify');
            vm.showVerify = showVerify_ == 'true';
            const path = to.path;
            vm.selectNav = vm.navList.findIndex(item => {return path.split('/')[1] == item.href});
            vm.loginName = localStorage.getItem('loginName');
            vm.marsCurrency = localStorage.getItem('MarsCurrency');

            vm.auto = async () => {
                vm.userDesc();
                vm.timeout = setTimeout(function() {
                    if (vm.auto)
                        vm.auto();
                }, 1000)
            }

            vm.auto1 = async () => {
                await vm.handleGetInfo();
                vm.handleGetNotice();
                vm.handleGetMarsMoney();
                vm.timeout1 = setTimeout(function() {
                    if (vm.auto1)
                        vm.auto1();
                }, 60000)
            }

            vm.auto();
            vm.auto1();
        })
    },
    beforeRouteLeave (to, from, next) {
        // ...
        let that = this;

        that.auto = null;
        that.auto1 = null;
        clearTimeout(that.timeout);
        clearTimeout(that.timeout1);
        that.timeout = null;
        that.timeout1 = null;
        next();
    },
    mounted() {
        let that = this;

        Bus.$on('changeActive', data => {
            that.selectNav = that.navList.findIndex(item => item.href === data);
        })
    },
    methods: {
        handleGetMarsMoney() {
            let that = this;

            that.$axios({
                url: '/api/Merchant/GetMerchantCurrency'
            }).then(function(data) {
                if (data.data.Status === 100) {
                    that.marsCurrency = data.data.Keyword;
                    localStorage.setItem('MarsCurrency', data.data.Keyword);
                }
            })
        },
        handleLock() {
            let that = this;
            let openVerify = localStorage.getItem('openVerify');
            if (openVerify != 'true') {
                that.$Message.error('');
                return;
            } else if (openVerify == 'true') {
                localStorage.setItem('againVerify', true);
                that.showVerify = true;
            }
        },
        handleUnlockSuccess() {
            let that = this;
            that.showVerify = false;
        },
        handleGetNotice() {
            let that = this;

            that.$axios({
                url: '/api/Merchant/GetAgentNotice'
            }).then(function(data) {
                if (data.data.Status === 100) {
                    that.sysBulletin = data.data.Keyword;
                }
            })
        },
        userDesc() {
            let that = this;

            that.serveTime = new Date().pattern('yyyy-MM-dd HH:mm:ss');
        },
        handleGetInfo() {
            return new Promise((resolve, reject) => {
                let that = this;

                that.$axios({
                    url: '/api/Merchant/GetMerchantInfos'
                }).then(data => {
                    if (data.data.Status === 100) {
                        const result = data.data.Model;

                        that.sysInfo.totalProfitAndLoss = result.ProLoss;
                        that.sysInfo.totalBalanceOfAccount = result.AllAmount;
                        that.sysInfo.poptip = result.MaturityTime;
                    }
                    resolve();
                })
            })
        },
        handleGetAmount() {
            return new Promise((resolve, reject) => {
                let that = this;

                that.$axios({
                    url: '/api/User/GetUserBalance',
                    params: {
                        loginName: that.loginName
                    }
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        const result = data.data.Keyword.split('');
                        that.balance = result[result.length - 1];
                    }
                    resolve();
                })
            })
        },
        handleExit() {
            let that = this;
            that.axios({
                url: '/api/Merchant/MerchantExit',
                method: 'put'
            }).then(function(data) {
                if (data.data.Status === 100) {
                    that.$router.push({name: 'login'});
                }
            })
        },
        handleNav(item, ind) {
            let that = this;
            if (that.selectNav !== ind) {
                that.selectNav = ind;
                if (item.href == 'other') {
                    Bus.$emit('changeSelect')
                }
                that.$router.push({ name: item.href });
            }
        }
    },
}
</script>

<style lang="scss">
@keyframes wordsLoop {
    0% {
        transform: translateX(100%);
        -webkit-transform: translateX(100%);
    }

    100% {
        transform: translateX(-100%);
        -webkit-transform: translateX(-100%);
    }
}
.con {
    .header {
        display: flex;
        align-items: center;

        padding: 0 20px;

        height: 50px;

        background-color: rgb(25, 25, 25);
        border: 1px solid rgba(102, 102, 102, 0);

        .item {
            margin-right: 10px;
        }

        .money {
            cursor: help;
            & > img {
                width: 20px;
                height: 20px;
                vertical-align: middle;
            }

            & > span {
                margin-left: 8px;
                vertical-align: middle;
                color: #a1a1a1;
            }
        }

        .logo {
            width: 42px;
            height: 42px;
            & > img {
                width: 42px;
                height: 42px;
            }
        }

        .time {
            width: 230px;

            text-align: right;

            & > p:first-child {
                color: rgb(255, 153, 0);
            }

            & > p:last-child {
                color: rgb(102, 102, 102);
            }
        }

        .notice {
            position: relative;

            flex-grow: 1;

            height: 36px;
            line-height: 36px;

            border-radius: 5px;
            background-color: rgb(40, 40, 40);
            border: 1px solid rgb(67, 67, 67);
            overflow: hidden;

            .notice-bulletin {
                position: absolute;

                width: auto;
                min-width: 100%;
                height: 36px;
                line-height: 36px;

                white-space: nowrap;

                animation: 30s wordsLoop linear infinite normal;
                font-size: 17px;
                color: #fff;
            }
        }

        .user {
            display: flex;
            align-items: center;

            & > span:first-child {
                margin-right: 8px;

                font-family: jdIonicons;
                font-size: 24px;
                color: rgb(102, 102, 102);
            }

            & > span:last-child {
                color: rgb(161, 161, 161);
            }
        }

        .exit {
            width: 70px;
            height: 28px;
            line-height: 28px;

            background-color: rgb(40, 40, 40);
            border-radius: 4px;
            border: solid 1px rgb(102, 102, 102);
            color: rgb(242, 242, 242);
            text-align: center;
            cursor: pointer;
        }
    }

    .nav {
        padding-left: calc(100vw - 100%);

        background-color: transparent;
        text-align: center;

        .nav-list {
            display: inline-flex;
            align-items: center;
            justify-content: center;

            background-color: #f7f8fc;
        }

        .nav-item {
            margin-right: 6px;

            width: 73.5px;
            height: 86px;

            // border-radius: 5px;
            // background-color: rgb(228, 228, 228);
            // border: 1px solid rgb(215, 215, 215);
            text-align: center;
            cursor: pointer;

            & > img {
                margin-top: 10px;

                width: 50px;
                height: 50px;
            }

            & > p:first-child {
                font-size: 40px;
                color: rgb(102, 102, 102);
            }

            & > p:last-child {
                line-height: 14px;
            }
        }

        .nav-item:last-child {
            margin-right: 0;
        }

        .active-item {
            background-color: #32b3ec;
            border-bottom: solid 4px #1dfcff;
            color: #feea48;

            & > p {
                text-shadow: 0 1px rgba(105, 82, 3, .45);
            }
        }
    }

    .page {
        margin: auto;
        padding-top: 10px;
        padding-left: calc(100vw - 100%);

        width: 950px;

        text-align: center;
        background-color: transparent;
        box-sizing: content-box;
    }
}
</style>